// pagination component

.pagination {
    margin: 30px 0 60px;
    &-page {
        &_link {
            width: 36px;
            height: 36px;
            border-radius: 8px;
            color: $primary;
            font-size: 17px;
            font-family: $accentFont;
            font-weight: 700;
            &:hover, &:focus,
            &[data-current="true"] {
                box-shadow: 1px 2px 15px rgba(33, 72, 66, 0.07);
            }
        }
    }
}

@media screen and (min-width: 991.98px) {
    .pagination {
        margin: 60px 0 0;
    }
}